/*html, body { height: 100%; width: 100%; margin: 0; padding: 0; }*/
body {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    overflow: hidden;
    color:#ffffffdd;
}

p {
    padding: 0px;
    margin-block-start: 0em;
    margin-block-end: 0em;
}

.question-types {
    margin-left: 3%;
    width: 70%;
}

.introduction .question-block {
    padding: 10px 10px 20px 10px;
    margin: 10px;
    box-shadow: 0 1px 5px black;
    background-color:#eeeeee;
    color:#000;
}

.introduction {
    width: 100vw;
    height: 100vh;
    overflow: auto;
    animation: intro-reveal 1s ease-in-out forwards;
    background-color: #464646;
    color:#ffffff;
}
.conclusion {
    overflow: auto;
    animation: intro-reveal 1s ease-in-out forwards;
    background-color: #464646;
    color:#ffffff;
}

@keyframes intro-reveal {
    from {
        opacity:0;
    }
}

.button-begin {
    float: right;
    text-decoration: none;
    text-align: center;
    font-size: 18px;
    background-color: rgba(128, 128, 128, 0.616);
    color:rgb(255, 255, 255);
    text-shadow: 0px 0px 5px rgb(255, 255, 255, 0.5);
    border-radius: 20px;
    width: 120px;
    padding: 10px;
    margin: 10px;
    margin-left: 100%;
    font: 20px/20px sans-serif;
    animation: pulsate-begin 1s ease-out infinite;
    cursor: pointer;
}

a.button-begin:hover {
    box-shadow: 3px 4px 5px rgba(0, 0, 0, 0.3);
    background-color: rgb(220, 220, 220);
    color: black;
    text-shadow: none;
}

@keyframes pulsate-begin {
    0%   { box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.5); }
    50%  { box-shadow: 0px 0px 15px rgb(255, 255, 0); }
    100% { box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.5); }
}

.introduction p {
    font: 18px/28px sans-serif;
    padding: 0px 30px 0px 30px;
}

.conclusion p {
    font: 18px/28px sans-serif;
    padding: 0px 30px 0px 30px;
}

.h1 {
    font: bold 38px/48px sans-serif;
    color: rgb(255, 255, 255);
    text-shadow: 2px 2px 5px black;
    text-align: center;
    padding: 20px 20px 0px 20px;
}

.h2 {
    font: bold 28px/34px sans-serif;
    color: rgb(255, 187, 0);
    text-shadow: 0 0 3px black;
    padding: 0px 15px 0px 15px;
}

#statement span {
    color:#111;
}

.redacted{
    background-color: #111;
    cursor: pointer;
}

/*.redacted:hover{
    background-color: rgb(128, 128, 128);
}*/

.item-history { 
    grid-area: 2 / 1 / 2 / 1;
    min-width: 400px;
    min-height: 0;
}
.item-picture { 
    grid-area: 2 / 2 / 2 / 2;
    width:100%;
    height:100%;
    min-width: 200px;
    min-height: 0;
}
.item-questions { 
    grid-area: 3 / 1 / 3 / 3; 
    min-width: 0;
    min-height: 0;
}

.statement-header {
    display: inline-block;
    color: rgb(29, 76, 138);
    font: 18px/28px sans-serif;
    padding: 10px;
    /*background-color: red;*/
}

.statement-header.right {
    float: right;
    margin-left: auto;
    text-align: end;
    /*background-color: blue;*/
}

.statement-footer {
    display: inline-block;
    color: rgb(29, 76, 138);
    font: 18px/28px sans-serif;
    padding: 10px;
    /*background-color: red;*/
    width: 50%;
}

.signature {
    font: italic small-caps 32px/5px cursive;
    display: inline-block;
}

.statement-container
{
    position: relative;
    /*transform: rotate(2.5deg) skewY(-0.5deg);*/
    width: 50%;
    min-width:550px;
    margin:50px;
    padding:20px 40px 20px 40px;
    overflow: auto;
    background-color: #ffffff;
    border-bottom-right-radius: 150px 10px;
    box-shadow: 2px 25px 25px rgb(0, 0, 0, 0.4);
}

.statement-grid
{
display: grid;
/*grid-template-rows: 5% 45% 50%;*/
grid-template-columns: 0.2fr 0.8fr;
/*grid-gap: 5px;*/
width: auto;
height: auto;
padding: 1px;
background-color: rgb(133, 133, 133);
color:#111;
}

.recap-page
{
    padding: 0px;
    width: 100vw;
    height: 100vh;
    background-color: #666;
}

.recap-page > .scrollBox {
z-index: -1;
}

.statement-hint {
    position: relative;
    height:auto;
    display: block;
    background-color:#666;
    padding:20px;
    /*border-bottom-right-radius: 50px 5px;
    border-bottom-left-radius: 80px 8px;*/
    box-shadow: 2px 10px 25px rgb(0 0 0 / 50%);
    pointer-events: none;
    animation: unroll 0.6s ease-in-out forwards;
}

@keyframes unroll {
    from { 
        opacity: 0;
    }
 }

 .hint-header {
    position: relative;
    height: auto;
    left: 50%;
    top: 0;
    margin-top: 30px;
    margin-bottom: -30px;
    font: 24px/24px sans-serif;
    transform: translate(-50%, 0);
    text-align: center;
    animation: pulsate-text 1.5s ease-in-out infinite;
 }

 @keyframes pulsate-text {
    0%      { text-shadow: 0px 0px 3px rgb(0, 0, 0); color: rgb(226, 160, 35)}
    50%     { text-shadow: 0px 0px 5px rgb(255, 255, 0); color: rgb(255, 255, 255);}
    100%    { text-shadow: 0px 0px 3px rgb(0, 0, 0); color: rgb(226, 160, 35)}
}


.recap-page-feedback {
position: fixed;
width: 40%;
height: auto;
right: 0;
top: 50%;
margin: 40px;
transform: translate(0, -50%);
/*transition: height 5s linear;*/
}

.statement-grid > div
{
    background-color: white;
    margin: 1px;
    padding: 10px;
    grid-area: auto;
    font: 18px/24px sans-serif;
}

.statement-grid.field-name {
    color: rgb(29, 76, 138);
    font: 24px/32px sans-serif;
}

.grid-container
{
display: grid;
grid-template-columns: 2.5fr 1fr;
grid-template-rows: 0.7fr 7fr 8fr;
/*grid-template-rows: 5% 45% 50%;*/
/*grid-gap: 5px;*/
width: 100vw;
height: 100vh;
background-color: #464646;
}

.grid-container > div {
border-style: solid;
border-color: #666;
background-color: rgb(77, 77, 77);
text-align: center;
/*padding: 20px 0;*/
/*font-size: 14px;*/
}

/* Box styles */
.scrollBox {
width: 100%;
height: 100%;
border: none;
padding: 0px;
font: 20px/23px sans-serif;
overflow: auto;
box-shadow: inset 3px 4px 5px rgba(0, 0, 0, 0.3);
}

/* icon style */
.tronIcon {
display:block;
float: right;
width:42px;
height:42px;
grid-area: 1/2;
margin-top: auto;
margin-left: auto;
}

.HistoryScrollBox {
overflow: auto;
}

.history-exchange {
animation: reveal-history 0.6s 1s ease-in-out forwards;
opacity: 0;
display: grid;
grid-template-columns: 10fr 1fr;
width: 100%;
margin-bottom: 20px;
}

.history-question {
animation: reveal-history 0.1s 0s ease-in-out forwards;
opacity: 0;
display: block;
float: left;
background-color: #90a3aa;
border-radius: 20px;
width: 70%;
text-align: left;
margin: 10px;
}

.history-answer {
animation: reveal-history 0.3s 1.5s ease-in-out forwards;
opacity: 0;
background-color: #aa8c7c;
border-radius: 20px;
width: 80%;
text-align: left;
display: block;
margin-left: auto;
grid-area: 1/1;
}

@keyframes reveal-history {
    from { 
        opacity: 0;
        transform: scale(0.9);
    }
    to {
        opacity: 1;
    }
 }

/* Scrollbar styles */
::-webkit-scrollbar {
width: 24px;
height: 24px;
}

::-webkit-scrollbar-track {
border: 2px solid #ffffff2f;
border-radius: 10px;
}

::-webkit-scrollbar-thumb {
background: rgb(255, 255, 255);
border-radius: 20px;
}

::-webkit-scrollbar-thumb:hover {
background: #ffffff;
}

.tronPlaceholder {
    width: 100%; /* image box size as % of container, see step 1 */
    height: 100%; /* image box size as % of container, see step 1 */
    object-fit: cover;
    display: block;
}

